import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import PropTypes from 'prop-types';

// Import Style
import { TextComp } from './styled';

const Text = ( props ) => (
  	<TextComp {...props}>
  		{props.text}
  		<strong>{props.number}</strong>
  	</TextComp>
);
Text.propTypes = {
  children: PropTypes.string
};
export default(Text);
